import React from "react";

const impBarStyle = {
  display: "flex",
  width: "100%",
  margin: "0%",
};

const barItems = {
  margin: "auto",
  padding: "1%",
  fontWeight: "bold",
};

const ImpBar = (props) => {
  const { tasks, setDisplay } = props;

  // Setting the Display according to the Importance of the Task
  const filter = (i) => {
    console.log("The imp value set: ", i);
    if (i !== "All") {
      console.log("THE TASKS TO START WITH: ", tasks);
      const filtered = tasks.filter((t) => t.importance === i);
      setDisplay(filtered);
    } else {
      setDisplay(tasks);
    }
  };

  const impList = ["Not Important", "Important", "Very Important", "All"];

  return (
    <div className="impBar" style={impBarStyle}>
      {impList.map((i) => (
        <span
          key={i}
          style={barItems}
          onClick={() => {
            filter(i);
          }}
        >
          {i}
        </span>
      ))}
    </div>
  );
};

export default ImpBar;
